import { auth } from './server';
import sql from '@/lib/db';
import { redirect } from 'next/navigation';

/** Only these emails can access /admin and admin APIs */
export const ADMIN_EMAILS = ['collinsnnaji1@gmail.com', 'speak2tojo@gmail.com'] as const;

export function isAdmin(email: string | null | undefined): boolean {
  return !!email && ADMIN_EMAILS.includes(email as any);
}

export async function getSession() {
  return auth.getSession();
}

export async function requireAuth() {
  const session = await auth.getSession();
  if (!session?.user) redirect('/auth/sign-in');
  return session.user;
}

export async function requireAdmin() {
  const user = await requireAuth();
  if (!isAdmin(user.email)) redirect('/dashboard');
  return user;
}

/** Upsert Neon Auth user into app_users and return their row */
export async function syncUser(authUser: { id: string; email: string; name?: string | null; image?: string | null }) {
  const role = isAdmin(authUser.email) ? 'admin' : 'client';
  const rows = await sql`
    INSERT INTO app_users (id, email, name, image, role)
    VALUES (${authUser.id}, ${authUser.email}, ${authUser.name ?? null}, ${authUser.image ?? null}, ${role})
    ON CONFLICT (email) DO UPDATE
      SET name  = EXCLUDED.name,
          image = EXCLUDED.image,
          id    = EXCLUDED.id
    RETURNING *
  `;
  return rows[0];
}

/** Get app_user row, syncing from auth if needed */
export async function getAppUser(authUser: { id: string; email: string; name?: string | null; image?: string | null }) {
  const rows = await sql`SELECT * FROM app_users WHERE email = ${authUser.email}`;
  if (rows.length) return rows[0];
  return syncUser(authUser);
}
